.card > img {
    width: 150px;
    height: 150px;
    border-radius: 75px;
    margin-bottom: 10px;
}

.card > h3 {
    margin: 0;
}

.card > p {
    margin: 0;
    font-size: 0.75em;;
}

.cameraid {
    padding-top: 3%;
}

.card {
    background:#0b0b0d;
    /* background: #d4eef1; */
    /* border: 1px solid #d3d5fd; */
    border-radius: 20px;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
    display: flex;
    flex-direction: column;
    height: 275px;
    /* width: 75%; */
    align-items: center;
    margin: 1%;
    padding: 20px;
    transition: transform 0.2s ease-out;
}

.card:hover {
    transform: scale(1.05);
    border: 3px solid #bec0f8;
    padding: 17px;
    cursor: pointer;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.05) inset, 0px 0px 10px rgba(82, 168, 236, 0.6);
}

.title {
    text-decoration: underline;
}

.fieldOne{
    margin-top: 2%;
}

.container {
    overflow: hidden;
}

.card-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-content: flex-start;
    /* padding-top: 80px; */
    overflow-y: auto;
    -ms-overflow-style: none;
    scrollbar-width: none;
    height: 100vh;
    z-index: 1;
}

.container::-webkit-scrollbar { 
    display: none;
}

