    border: 2px solid #d3d5fd;
    border-radius: 8px;
    color: white;
    text-align: center;
    font-size: 0.8em;
}
.go-back{cursor:pointer;height:45px;transition:transform .1s ease-in-out}.go-back:hover{transform:scale(1.08)}.go-back:active{transform:scale(1)}
.back-arrow{cursor:pointer;height:45px;transition:transform .1s ease-in-out}.back-arrow:hover{transform:scale(1.08)}.back-arrow:active{transform:scale(1)}
.forward-arrow{cursor:pointer;padding:0 5px;height:45px;rotate:180deg;transition:transform .1s ease-in-out}.forward-arrow:hover{transform:scale(1.08)}.forward-arrow:active{transform:scale(1)}

.clear-button{display:flex;align-items:center;justify-content:center;cursor:pointer;height:35px;width:150px;background:dimgray;border:none;border-radius:20px;color:#fff;font-family:Orbitron,sans-serif;transition:transform .1s ease-in-out;font-size:.75em}.clear-button:hover{transform:scale(1.025)}.clear-button:active{transform:scale(1)}
.menu-icon-wrapper{display:flex;justify-content:center;align-items:center;height:43px;width:43px;border-radius:24px;background:#fff}.menu-icon-wrapper .menu-icon{cursor:pointer;height:49px;transition:transform .1s ease-in-out}.menu-icon-wrapper .menu-icon:hover{transform:scale(1.08)}.menu-icon-wrapper .menu-icon:active{transform:scale(1)}
.line-break{height:0px;color:#fff;width:80%;padding:0px;margin:0 auto}.mini-card{cursor:pointer;display:flex;flex-direction:row;align-items:center;width:100%;padding:10px 0px;background:#0b0b0d}.mini-card:hover{background:#28282e}.mini-card .left-container{display:flex;margin-right:35px;margin-left:15px}.mini-card .left-container .index{margin-right:0px;margin-left:10px}.mini-card .left-container .image{width:100px;height:100px;border-radius:50px;margin-bottom:0px}.mini-card .info-container{display:flex;flex-direction:column;align-content:space-between}.mini-card .info-container h3{font-size:1.3em;margin:0;padding:1px}.mini-card .info-container h5{margin:0;padding:1px}.mini-card .info-container p{margin:0;padding:3px}.mini-card p{font-size:.75em}
.dropdown-list-wrapper .dropdown-list-back{background:#0b0b0d;border:2px solid #d3d5fd;border-radius:20px;box-shadow:0px 1px 4px rgba(2,2,2,.1) inset,0px 0px 15px rgba(12,12,12,.9);display:flex;flex-direction:column;height:80vh;width:350px;margin:5px 0 0 0;padding:0px;transition:transform .2s ease-out;overflow-y:auto;-ms-overflow-style:none;scrollbar-width:none}
.bar{display:flex;flex-direction:row;align-items:center;justify-content:space-between;width:100%;height:60px;background:#0c0c0c;z-index:2}.bar .left-container{width:40%}.bar .left-container .go-back-container{padding-left:40px}.bar .clear-button-container{padding-right:60px}.bar .right-container{display:flex;flex-direction:row;justify-content:flex-end;align-items:center;width:40%}.bar .right-container .dropdown-list-container .menu-icon-container{padding-right:40px;padding-bottom:2px}.bar .right-container .dropdown-list-container .dropdown-container{position:absolute;right:20px;top:0px;margin-top:60px;z-index:1}.bar .right-container .page-counter{font-size:.9em}.bar .right-container .arrows{text-align:right;padding:0 30px 0 15px}
.assetImage {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    width: 180px;
    height: 180px;
    margin: 20px;
    border-radius: 90px;
}

.header {
    display: grid;
    /* justify-content: center; */
    align-content: center;
    grid-template-columns: 1fr 10fr;
    background: #0b0b0d;
    border-radius: 20px;
    padding: 10px;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
}
.sub-info {
    background: #0b0b0d;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
    border-radius: 20px;
}
.details {
    display: grid;
    grid-template-rows: repeat(6, 1fr);
    grid-template-columns: 1fr 1fr;
    grid-column: span 2;
    align-items: center;
    background: #0b0b0d;
    padding: 10px;
    border-radius: 20px;
    box-shadow: 0px 1px 4px rgba(2, 2, 2, 0.1) inset, 0px 0px 15px rgba(12, 12, 12, 0.9);
    /* border-top: 1px solid #e4e5fc; */
}

.detailBox {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.error-container{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#fff;padding:20px 0}.error-container .error-page{color:#fff;margin:20px 0}.error-container .error{font-size:5em}.error-container .not-exist{font-size:2em}.error-container .link-page{color:#888}
